/* TABS */

h2#wp-optimize-nav-tab-wrapper {
    margin-bottom: 0;
	border-bottom: none;
	position: relative;
	
	.nav-tab,
	.nav-tab:hover,
	.nav-tab:focus,
	.nav-tab:focus:active {
		border: none;
		background: transparent;
		margin: 0;
		border-top: 3px solid transparent;
		padding: 7px 15px;
		color: $wp-blue;

		.dashicons {
			display: block;
			margin: 0 auto;
			color: $wp-secondary-gray;
			font-size: 30px;
			width: 30px;
			height: 30px;
		}
	}
	
	.nav-tab-active,
	.nav-tab-active:hover,
	.nav-tab-active:focus,
	.nav-tab-active:focus:active {
		background: #FFF;
		box-shadow: 0 0 1px rgba(0, 0, 0, 0.04);
		border-top-color: $wp-blue;

		.dashicons {
			color: $wp-blue;
		}
	}

	@media (max-width: $breakpoint-small) {
		a:not(.nav-tab-active) {
			display: none;
		}
	}
	
	a[role="toggle-menu"] {
		display: block;
		float: right;
		@media (min-width: $breakpoint-small) {
			display: none;
		}
	}


	a.nav-tab.wp-optimize-nav-tab__back {
		&, &:focus:active {	

			text-align: right;

			.dashicons {
				color: $wp-light-gray;
				font-size: 18px;
				display: inline-block;
				height: auto;
				line-height: inherit;
				width: 20px;
			}

			@media (min-width: $breakpoint-small) {
				position: absolute;
				bottom: 14px;
				right: 0;
				font-size: 12px;
				font-weight: 400;
				text-decoration: none;
				padding: 0;
			
				.dashicons {
					color: $wp-light-gray;
					font-size: 18px;
					display: inline-block;
					height: auto;
					line-height: inherit;
					width: 20px;
				}
			}
	
		}
	
	}
}

.wpo-mobile-menu-opened {

	.wpo-main .wpo-tab-postbox {
		display: none;
	}
	
	h2#wp-optimize-nav-tab-wrapper a {
		display: block;
		float: none;

		&.nav-tab-active,
		&.nav-tab-active:focus {
			border-top: none;
			border-left: 3px solid;
		}
		
		&.nav-tab-active,
		&.nav-tab-active:focus,
		&:focus,
		&:hover,
		& {
			.dashicons {
				display: inline-block; 
				line-height: inherit;
			}
		}

		&[role="toggle-menu"] {
			display: none;
		}
	}

}


/* Pages MENU */

.wpo-pages-menu {
	position: absolute;
	bottom: 0;
	right: 0;
	display: none;

	@media (max-width: 820px) {
		.opened + & {
			display: block;
			box-shadow: 0 5px 25px rgba(0, 0, 0, 0.17);
		}
	}

	@media (min-width: 821px) {
		display: flex;
		height: 77px;
	}

	> a {
		text-decoration: none;
		position: relative;
		color: inherit;
		text-align: center;
		box-sizing: border-box;
		display: block;
		color: $wp-gray-dark;

		@media (min-width: 821px) {
			padding: 0 8px;
			height: 77px;
			display: flex;
			flex-direction: column;
			justify-content: center;

			> span {
				display: block;
				margin: 0 auto;
			}
		}

		@media (max-width: 820px) {
			padding: 15px;
			font-size: 1.2em;
		}

		&.active {
			&::after {
				content: '';
				display: block;
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 3px;
				background: $brand;
				color: $wp-gray-darker;

				@media (max-width: 820px) {
					width: 3px;
					height: 100%;
				}

			}
			@media (max-width: 820px) {
				color: $brand;
			}
		}

		&:hover {
			background: #F9F9F9;
			color: $wp-gray-darker;
		}
	}

	span.separator {
		display: block;
		width: 2px;
		background: #EFEFEF;
		margin-top: 18px;
		margin-bottom: 18px;
		margin-right: 5px;
		margin-left: 5px;
		@media (max-width: 820px) {
			width: 80%;
			height: 2px;
			margin: 10px 10%;
		}
	}

	@media (max-width: 820px) {
		text-align: center;
		top: 100%;
		width: 100%;
		bottom: auto;
		background: #FFF;		

	}

	body.rtl & {
		left: 0;
		right: auto;
	}
}

#wp-optimize-nav-page-menu {
	position: absolute;
	right: 0;
	bottom: 0;
	display: flex;
	height: 77px;
    flex-direction: column;
    padding: 0 20px;
    align-items: center;
    justify-content: center;
	text-decoration: none;

	&:not(.opened) .dashicons-no-alt {
		display: none;
	}

	&.opened .dashicons-menu {
		display: none;
	}

	@media (min-width: 821px) {
		display: none;
	}

	body.rtl & {
		right: auto;
		left: 0;
	}
}

/* DASHBOARD Menu */

/* Calculate column width with a $spacing gutter */
.wpo-dashboard-pages-menu {
	display: flex;
	flex-wrap: wrap;

	/* Same size for 1 to 3 items */
	&[data-itemscount="1"],
	&[data-itemscount="2"],
	&[data-itemscount="3"] {
		.wpo-dashboard-pages-menu__item {
			width: calc(33.333% - 40px / 3);
		}
	}

	/* above 3 items, change sizes */
	@for $count from 4 to 6 {
		&[data-itemscount="$count"] {
			.wpo-dashboard-pages-menu__item {
				width: calc(100% / $count - $spacing * ($count - 1) / $count);
			}
		}
	}

	.postbox.wpo-dashboard-pages-menu__item {
		padding: $spacing;
		margin-right: $spacing;
		box-sizing: border-box;
		padding-bottom: 60px;
		min-width: 0;
	
		&:last-child {
			margin-right: 0;
		}
	
		h3 {
			margin-top: 0;

			.dashicons {
				color: $wp-secondary-gray;
				line-height: 1;
				height: 18px;
				margin-top: -2px;
				margin-right: 10px
			}

		}

		a {
			position: absolute;
			bottom: $spacing;
			left: $spacing;
			width: calc(100% - $spacing * 2) !important;
		}

		@media (max-width: $breakpoint-small) {
			width: 100%;
			margin-right: 0;
			padding-bottom: 20px;

			a.button.button-large {
				width: auto !important;
				left: auto;
				bottom: auto;
				top: 50%;
				transform: translateY(-50%);
				right: $spacing;
			}

			h3 {
				margin: 0;
			}

			p {
				display: none;
			}

		}
	
	}

}

/* Admin bar separator */

#wpadminbar .quicklinks .menupop.hover ul li.separator .ab-item.ab-empty-item {
    height: 2px;
    line-height: 2px;
    background: #ffffff17;
    margin: 5px 10px;
    width: auto;
    min-width: 0;
}