body[class*="toplevel_page_WP-Optimize"] #wpbody,
body[class*="wp-optimize_page_"] #wpbody {
	padding-right: 15px;
	@media screen and (max-width: 782px) {
		padding-right: 10px;
	}
}

body.toplevel_page_WP-Optimize.rtl #wpbody {
	padding-left: 15px;
	padding-right: 0;
	@media screen and (max-width: 782px) {
		padding-left: 10px;
	}
}

body[class*="toplevel_page_WP-Optimize"] #wpbody-content,
body[class*="wp-optimize_page_"] #wpbody-content {
	padding-top: 80px;

	@media (max-width: 600px) {
		padding-top: 0;
	}	

	@media (min-width: 820px) {
		padding-top: 100px;
	}	
}

#wp-optimize-wrap {
	position: relative;
	padding-top: 20px;

	@media (max-width: 600px) {
		padding-top: 110px;
	}	

	@media(max-width: $breakpoint-small) {
		margin-right: 0;
	}
}

/* DASHBOARD */

.wpo-main-header {

    height: 77px;
    position: fixed;
    top: 32px;
    left: 160px;
    background: #FFF;
    right: 0;
    z-index: 9980;

	@media (min-width: 820px) {
		height: 105px;
	}	

	.wpo-logo__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		height: 100%;
		line-height: 1;
		font-size: 1rem;
		padding-left: 50px;
		margin-left: 10px;
	}

	.wpo-logo {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 50px;
	}

	.wpo-subheader {
		margin: 0;
		font-weight: 300;
		color: $wp-secondary-gray;
		line-height: 1.3;

		@media (max-width: 1080px) {
			display: none;
		}
	}

	p.wpo-header-links {
		margin: 0;
		font-size: 0.7rem;
		position: absolute;
		right: 0;
		padding: 6px 15px;
		background: #EDEEEF;
		border-bottom-left-radius: 5px;
		z-index: 1;

		a {
			text-decoration: none;
		}

		.wpo-header-links__label {
			color: $wp-secondary-gray-light;
			position: absolute;
			right: 100%;
			width: 110px;
			text-align: right;
			padding-right: 10px;
		}

		@media (max-width: 820px) {
			display: none;
		}
	}

	p.wpo-header-links__mobile {
		padding: 10px;
		background: #EDEEEF;
		margin-bottom: 0;
		@media (min-width: 820px) {
			display: none;
		}

		a {
			display: inline-block;
			padding: 4px;
			font-size: .8rem;
		}
		.wpo-header-links__label {
			color: $wp-secondary-gray-light;
		}

	}

	@media (max-width: 600px) {
		
		.wpo-logo__container strong {
			width: 140px;
			display: inline-block;
		}

	}

	body.auto-fold & {
		@media (max-width: 960px) {
			left: 36px;
		}
		@media (max-width: $breakpoint-small) {
			left: 0;
			top: 46px;
		}
		@media (max-width: 600px) {
			position: absolute;
			left: -10px;
			right: -10px;
			top: 10px;
		}
	}

	body.auto-fold #screen-meta + #wp-optimize-wrap & {
		@media (max-width: 600px) {
			top: 0;
		}
	}

	body.folded & {
		@media (min-width: $breakpoint-small) {
			left: 36px;
		}
	}
	body.is-scrolled & {
		box-shadow: 0 5px 25px rgba(0, 0, 0, 0.17);
	}

	/* RTL */
	body.rtl & {
		right: 160px;
		left: 0;

		.wpo-logo__container {
			padding-left: 10px;
			padding-right: 50px;
			margin-left: 0;
			margin-right: 10px;
		}
	
		.wpo-logo {
			position: absolute;
			left: auto;
			right: 0;
		}	

		p.wpo-header-links {
			right: auto;
			left: 0;
			border-bottom-right-radius: 5px;
			border-bottom-left-radius: 0;
		
			.wpo-header-links__label {
				position: absolute;
				left: 100%;
				right: auto;
				width: 110px;
				text-align: left;
				padding-left: 10px;
			}
		}
	}

	body.rtl.auto-fold & {
		@media (max-width: 960px) {
			right: 36px;
		}
		@media (max-width: $breakpoint-small) {
			right: 0;
			top: 46px;
		}
		@media (max-width: 600px) {
			position: absolute;
			left: -10px;
			right: -10px;
			top: 10px;
		}
	}

	body.rtl.folded & {
		@media (min-width: $breakpoint-small) {
			right: 36px;
			left: 0;
		}
	}

}

.wpo-page {
	body.rtl & {
		padding-right: 0;
	}
	@media (min-width: 769px) {
	}

	&:not(.active) {
		display: none;
	}
}

/* Columns */
@media (min-width: 1200px) {

	.wpo-tab-postbox.right-col {
		width: 350px;
		float: right;
		box-sizing: border-box;
		margin-top: 3.1rem;
	}

	.right-col + .wpo-main {
		float: left;
		box-sizing: border-box;
		width: calc(100% - 380px);
	}

}
