/* Common BLOCKS */


/* Buttons */

#wp-optimize-wrap {

	.button-large {
		@media (max-width: $breakpoint-small) {
			display: block;
			width: 100%;
		}
	}

	.red {
		color: $red;
	}

	div[id*="_notice"] div.updated {
		margin-left: 0;
	}
}

.button.button-block {
	display: block;
	width: 100%;
	text-align: center;
}

.wpo-refresh-button {
	float: right;
}

.wpo-refresh-button:hover {
	cursor: pointer;
}

.wpo-refresh-button .dashicons {
	text-decoration: none;
    line-height: inherit;
    font-size: inherit;
}

/* Helper classes */

*[class*="wpo-badge"] {
	display: inline-block;
	font-size: .8em;
	text-transform: uppercase;
	background: $wp-lighter-gray;
	padding: 3px 5px;
	line-height: 1;
	border-radius: 3px;
}

.wpo-badge__new {
	background: #DBE3E6;
	color: #00689a;
}

.wpo-text__dim {
	color: $wp-light-gray;
}

.wpo-fieldgroup .wpo-text__dim {
	color: $wp-secondary-gray-light;
}

.wpo-first-child {
	margin-top: 0;
}

#save_done, .save-done {
	color: $success;
	font-size: 250%;
}

p.wpo-take-a-backup {
	display: inline-block;
	margin: 0;
	line-height: 1;
	padding-top: 8px;
	@media (min-width: $breakpoint-small) {
		margin-left: 20px;
	}
}

/* Postbox */

#wp-optimize-nav-tab-wrapper ~ .wp-optimize-nav-tab-contents > .postbox {
    border: none;

	> h3:first-child {
		margin-top: 0;
	}

}

.wpo-p25,
.postbox.wpo-tab-postbox {
	padding: 25px;
}

.wpo-tab-postbox {
	
	> h3:first-child {
		margin-top: 0;
	}
}

/* Field group */

.wpo-fieldgroup {
    background: $wp-lighter-gray;
    border-radius: 8px;
	padding: 20px;

	&:not(:last-child) {
		margin-bottom: 2em;
	}

	> *:first-child {
		margin-top: 0;
	}
	
	> *:last-child {
		margin-bottom: 0;
	}
}

.wpo-fieldgroup__subgroup {
	&:not(:last-of-type) {
		margin-bottom: 20px;
	}
}