/* More Plugins page */

/* Plugins family / Premium */

p.wpo-plugin-installed {
    color: $success;
}

/* Settings repeater */

.wpo-repeater__add {
	display: inline-block;
	cursor: pointer;
	font-weight: bold;
	text-decoration: none;
}


/* Plugin familly tab */

.wpo-plugin-family__premium h2 {
    margin: 0;
    padding: 25px;
}

.wpo-plugin-family__plugins {
    display: flex;
    flex-wrap: wrap;
	padding-left: 1px;
	padding-bottom: 1px;
}

.wpo-plugin-family__plugin {
    flex: auto;
    width: 100%;
    padding: 30px;
    box-sizing: border-box;
    border: 1px solid #E3E4E7;
	margin-left: -1px;
	margin-bottom: -1px;

	@media (min-width: $breakpoint-small) {
		width: 50%;

		.wpo-plugin-family__free & {
			width: 100%;
		}
	}

	@media (max-width: 1080px) {
		.wpo-plugin-family__free & {
			width: 50%;
		}
	}

	@media (max-width: $breakpoint-small) {
		.wpo-plugin-family__free & {
			width: 100%;
		}
	}

}

div#wp-optimize-nav-tab-wpo_mayalso-may_also-contents .wpo-tab-postbox {
    padding: 0;
}

/* Added for WPO Premium Features tab */
.wpo_feature_cont {
	width: 64.5%;
}

.wpo_plugin_family_cont {
	width: 34.5%;
}

@media (max-width: 1080px) {

	.wpo_feature_cont,
	.wpo_plugin_family_cont {
		width: 100%;
		float: none;
		margin: 0;
	}

}

.wpo_feature_cont,
.wpo_plugin_family_cont {

	header {
		padding: 20px;

		@media (max-width: 1080px) {
			padding: 40px;		
		}

		h2 {
			margin: 0;
		}
	
		p {
			margin-bottom: 0;
		}

	}

}

.wpo_feat_table, .wpo_feat_th, .wpo_feat_table td {
	border: none;
	border-collapse: collapse;
	background-color: white;
	font-size: 120%;
	text-align: center;
}

.wpo_feat_table {
	td {
		border: 1px solid #F1F1F1;
		border-bottom-width: 4px;
		padding: 15px;
	}

	td:nth-child(2), td:nth-child(3) {
		background: rgba(241, 241, 241, 0.38);
	}

	p {
		padding: 0px 10px;
		margin: 5px 0px;
		font-size: 13px;
	}

	h4 {
		margin: 5px 0px;
	}

	.dashicons {
		width: 25px;
		height: 25px;
		font-size: 25px;
		line-height: 1;
	}

	.dashicons-yes, .updraft-yes {
		color: green;
	}
	
	.dashicons-no-alt, .updraft-no {
		color: red;
	}

}



.wpo-premium-image {
	display: none;
}

@media screen and (min-width: 720px) {

	#wpoptimize_table_list_filter {
		width: 40%;
	}

	.wpo-premium-image {
		display: block;
		float: left;
		padding: 16px 18px;
		width: 30px;
		height: auto;
	}

}

@media screen and (min-width: 1220px) {

	.wpo_feat_table td:nth-child(2), .wpo_feat_table td:nth-child(3) {
		width: 110px;
	}

}

.other-plugin-title {
	text-decoration: none;
}

@media screen and (max-width: $breakpoint-small) {

	table.wpo_feat_table {

		display: block;

		tr {
			display: flex;
			flex-wrap: wrap;
		}

		td {
			display: block;

			&:first-child {
				width: 100%;
				border-bottom: none;
			}

			&:not(:first-child) {
				width: 50%;
				box-sizing: border-box;
			}

			&:first-child:empty {
				display: none;
			}

			&[data-colname]::before {
				content: attr(data-colname);
				font-size: 0.8rem;
				color: #CCC;
				line-height: 1;
			}
		}
	}

}