
/* tablesorter */

#wp-optimize-wrap {

	.wp-list-table {
		
		td {
			word-break: break-all; 
		}

	}

	@media screen and (max-width: $breakpoint-small ) {

		.wp-list-table.wp-list-table-mobile-labels tbody {

			&, tr, th, td {
				display: block;
			}

			td {
				padding: 3px 8px 3px 35%;
				word-break: break-word;
				box-sizing: border-box; 

				&::before {
					color: $wp-light-gray;
				}

			}

		}		

		.wp-list-table.wp-list-table-mobile-labels thead {

			&, tr {
				display: block;
			}

			th.column-primary {
				display: block;
				box-sizing: border-box; 
			}

			th:not(.column-primary) {
				display: none !important; 
			}

		}

	}

	body.rtl & {
		.wpo-table-list-filter {
			text-align: left;
		}
		@media screen and (max-width: $breakpoint-small ) {
			.wp-list-table.wp-list-table-mobile-labels tbody {
				td {
					padding: 3px 35% 3px 8px;
				}
			}
		}
	}
}

th:not(.sorter-false) .tablesorter-header-inner {

	color: #0074ab;

	&::after {
		content: '';
		font-family: 'dashicons';
		font-size: 20px;
		line-height: 20px;
		height: 20px;
		width: 20px;
		display: inline-block;
		vertical-align: bottom;
	}

}

th.tablesorter-header.tablesorter-headerAsc,
th.tablesorter-header.tablesorter-headerDesc {
    font-weight: 500;
    border-bottom-color: $wp-blue;
}

th.tablesorter-header.tablesorter-headerDesc .tablesorter-header-inner::after {
    content: "\f140";
}

th.tablesorter-header.tablesorter-headerAsc .tablesorter-header-inner::after {
    content: "\f142";
}

th.tablesorter-header:focus {
    outline: none;
	box-shadow: 0 0 3px rgba(0, 116, 171, 0.78);

	&:not(.tablesorter-headerAsc):not(.tablesorter-headerDesc) .tablesorter-header-inner::after {
		content: "\f142";
		opacity: 0.5;
	}

}

.tablesorter.hasFilters tr.filtered {
    display: none;
}
