/* Unused images / wpo_smush_image / image grid*/

.wpo_unused_image, .wpo_smush_image {
	position: relative;
	margin: 4px;
	width: calc(50% - 8px);
	text-align: center;

	@media (min-width: 500px) {
		width: calc(25% - 8px);
	}

	@media (min-width: $breakpoint-small) {
		width: calc(16.6666% - 8px);
	}

	@media (min-width: 1100px) {
		width: calc(12.5% - 8px);
	}

	.wpo_unused_image__input {
		position: absolute;
		top: 10px;
		visibility: hidden;
	}
	
	label {
		display: block;
		width: 100%;
		position: relative;
		padding: 1px;
		border: 3px solid $wp-lighter-gray;
		box-sizing: border-box;

		&::before {
			content: "";
			display: block;
			padding-top: 100%;
		}

		.thumbnail {
			position: absolute;
			top: 1px;
			left: 1px;
			width: calc(100% - 2px);
			height: calc(100% - 2px);
			background-repeat: no-repeat;
			background-size: cover;
			background-position: 50% 50%;
			overflow: hidden;
			background: rgba(220, 220, 220, 0.2);

			img {
				display: block;
				margin: 0;
				max-height: 100%;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				user-select: none;
			}

		}
		
	}
	
	.wpo_unused_image__input:checked + label,
	&.checked label {
		border-color: $wp-blue;
	}

	a, a.button, a.button:active {
		position: absolute;
		z-index: 2;
		bottom: 13px;
		left: 50%;
		transform: translateX(-50%);
		display: none;
	}

	&:hover {

		a, a.button, a.button:active {
			display: inline-block;
		}

	}

}

#wpo_unused_images, #wpo_smush_images_grid {
	width: calc(100% + 8px);
    margin-left: -4px;
    margin-right: -4px;
	max-height: 500px;
	overflow-y: auto;
    display: flex;
	flex-wrap: wrap;

	.wpo-fieldgroup {
		width: 100%;
	}

}

#wpo_unused_images a {
	outline: none;
}